@import url(//db.onlinewebfonts.com/c/a082fd3df68a0b54e0d4d794bc38d268?family=Blender+Pro);
@font-face {
  font-family: "Blender Pro";
  src: url("//db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.eot");
  src: url("//db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.woff") format("woff"), url("//db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/a082fd3df68a0b54e0d4d794bc38d268.svg#Blender Pro") format("svg");
}

html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  font-family:'Blender Pro', -apple-system,
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.cover-container {
  color: white;
  /*text-align: center; */
}

.hero-container {
  padding: 20px 0; }
@media (min-width: 576px) {
  .hero-container {
    padding: 20px 0; } }
.hero-container .hero-title {
  font-size: 3rem;
  font-weight: 300; }
.hero-container .hero-subtitle {
  display: none;
  margin: 2.14286rem 0;
  font-size: 1.71429rem;
  font-weight: 300; }
@media (min-width: 576px) {
  .hero-container .hero-subtitle {
    display: block; } }
.hero-container .hero-btn {
  margin-top: 2.14286rem; }

.landing {
  height: 100vh;
  max-width: 2000px;
  box-shadow: inset 0 0 400px rgba(31, 37, 50, 0.8);
  background: linear-gradient(rgba(31, 37, 50, 0.8), rgba(31, 37, 50, 0.8)), url(/plakias-menu/static/media/crete-beach.45192118.jpg);
  background-size: cover; }
@media (min-width: 576px) {
  .app.landing {
    background: linear-gradient(rgba(31, 37, 50, 0.7), rgba(31, 37, 50, 0.8)), url(/plakias-menu/static/media/crete-beach.45192118.jpg);
    background-size: cover; } }


